import { Component, OnInit } from '@angular/core';
import { Product } from 'src/app/models/product.model';
import { ResourceDeleteContext } from 'src/app/shared/dashboard-resource/resource-delete/resource-delete.component';

@Component({
  selector: 'app-product-delete',
  templateUrl: './product-delete.component.html',
  styleUrls: ['./product-delete.component.scss']
})
export class ProductDeleteComponent implements OnInit {
  context: ResourceDeleteContext<Product>;

  constructor() { }

  ngOnInit(): void {
    this.context = {
      deleteUrl: 'productsDeleteUrl',
      data: history.state.resource,
      title: 'Delete Product Resource!',
      description: 'Are you sure to delete this product?'
    };
  }

}
