import { Component, OnInit } from '@angular/core';
import { Product } from 'src/app/models/product.model';
import { IResourceDetailsContext } from 'src/app/shared/dashboard-resource/resource-details/resource-details.context';
import { ProductsResolver } from '../products.resolver';
import { AttachmentsViewerComponent } from 'src/app/shared/attachments/attachments-viewer/attachments-viewer.component';

@Component({
  selector: 'app-product-details',
  templateUrl: './product-details.component.html',
  styleUrls: ['./product-details.component.scss']
})
export class ProductDetailsComponent implements OnInit {
  public context: IResourceDetailsContext<Product>;

  ngOnInit(): void {
    const product: Product = ProductsResolver.Json2Product(history.state.resource);

    this.context = {
      urls: {
        tableUrl: '/products',
      },
      data: product,
      properties: [
        {
          key: 'name',
          title: 'Name',
          type: 'string'
        },
        {
          key: 'description',
          title: 'Description',
          type: 'string'
        },
        {
          key: 'brand.name',
          title: 'Brand',
          type: 'string'
        },
        {
          key: 'category.name',
          title: 'Cateogry',
          type: 'string'
        },
        {
          key: 'price.value',
          title: 'Price',
          type: 'float'
        },
        {
          key: 'created_at',
          title: 'Created At',
          type: 'datetime'
        },
        {
          key: 'updated_at',
          title: 'Updated At',
          type: 'datetime'
        },
        {
          key: 'attachments',
          title: 'Attachments',
          type: 'component',
          component: 'app-attachments-viewer',
        },
      ],
      title: `Details of product ${ product?.id }`
    };
  }
}
