import { Component, OnInit, ViewChild, ElementRef, AfterContentInit } from '@angular/core';
import { ResourceFormContent } from 'src/app/shared/dashboard-resource/resource-form/resource-form.component';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import { ActivatedRoute, Router } from '@angular/router';
import { NgbModal, NgbModalRef, NgbModalOptions } from '@ng-bootstrap/ng-bootstrap';
import { environment } from 'src/environments/environment';
import { Category } from 'src/app/models/category.model';
import { Brand } from 'src/app/models/brand.model';
import { Attachment } from 'src/app/models/attachment.model';
import { ProductsResolver } from '../products.resolver';

function validateAttachments(c: FormControl): any {
  if (!Array.isArray(c.value) || c.value.length < 1 || c.value.length > 4)
  {
    return {
      validateAttachments: {
        valid: false
      },
    };
  }
  return null;
}

@Component({
  selector: 'app-product-form',
  templateUrl: './product-form.component.html',
  styleUrls: ['./product-form.component.scss']
})
export class ProductFormComponent extends ResourceFormContent implements AfterContentInit {
  title: string;
  message: string;
  formGroup: FormGroup;

  brands: Brand[];
  categories: Category[];

  attachments: Attachment[];

  @ViewChild('content') content: ElementRef<HTMLElement>;

  constructor(http: HttpClient, route: ActivatedRoute, router: Router, service: NgbModal) {
    super(http, route, router, service);

    http.get<any[]>(environment.prepareUrl('brandsFetchUrl')).toPromise().then(res => this.brands = res);
    http.get<any[]>(environment.prepareUrl('categoriesFetchUrl')).toPromise().then(res => this.categories = res);

    this.formGroup = new FormGroup({
      name: new FormControl(null, Validators.required),
      description: new FormControl(null, Validators.required),
      brand_id: new FormControl(null, Validators.required),
      category_id: new FormControl(null, Validators.required),
      price: new FormControl(null, Validators.required),
      is_main: new FormControl(false, Validators.required),
      attachments: new FormControl(null, [Validators.required, validateAttachments]),
    });
  }

  ngAfterContentInit(): void {
    if (!history.state.resource)
    {
      return;
    }
    this.attachments = ProductsResolver.Json2Product(history.state.resource).attachments;

    setTimeout(() => {
      this.formGroup.controls.attachments.setValue(this.attachments.map(a => a.id));
    }, 500);
  }

  openModal(): NgbModalRef {
    const options: NgbModalOptions = {
      backdrop: 'static'
    };

    return this.service.open(this.content, options);
  }

  get postUrl(): string {
    return 'productsCreateUrl';
  }

  get putUrl(): string {
    return 'productsUpdateUrl';
  }

  get parentUrl(): string {
    return '/products';
  }

  initData(): void {
    super.initData();

    if (history.state.resource)
    {
      this.formGroup.patchValue({
        price: history.state.resource.price.value
      });
    }
  }

  setSelectedAttachment(e: { ids: number[], entities: Attachment[] }): void {
    if (!e || !e.ids || !e.entities || e.ids.length !== e.entities.length || e.ids.length > 4)
    {
      console.log(e);
      console.log(e.ids.length);
      this.attachments = null;
      this.formGroup.patchValue({
        attachments: null,
      });
      return;
    }
    this.attachments = e.entities;
    this.formGroup.patchValue({ attachments: e.ids });
  }
}

