import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Resolve, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';

import { environment } from '../../../environments/environment';
import { Product } from '../../models/product.model';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { Attachment } from 'src/app/models/attachment.model';

@Injectable()
export class ProductsResolver implements Resolve<Product[]> {
    public static Json2Product(element: any): Product {
        if (!element)
        {
            return null;
        }

        const attachments: Attachment[] = element?.attachments?.map((a: any) => new Attachment(a.id, a.type, a.url, a.name));
        return new Product(
            element?.id,
            element?.name,
            element?.description,
            element?.price_id,
            element?.price,
            element?.brand_id,
            element?.brand,
            element?.category_id,
            element?.category,
            element?.created_at,
            element?.updated_at, attachments);
    }

    constructor(private http: HttpClient) {
    }

    resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<Product[]> {
        return this.http.get<any[]>(`${ environment.apiUrl }/${ environment.productsFetchUrl }`)
            .pipe(map(data => data.map(e => ProductsResolver.Json2Product(e))));
    }
}
