import { Component, ViewChild } from '@angular/core';
import { Product } from 'src/app/models/product.model';
import { TableViewComponent } from 'src/app/shared/dashboard-resource/table-view/table-view.component';
import { ITableContext } from 'src/app/shared/dashboard-resource/table-view/table.context';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss']
})
export class IndexComponent {
  data: Product[];
  context: ITableContext<Product> = {
    urls: {
      createUrl: '/products/create',
      detailsUrl: '/:id'
    },
    data: this.data,
    properties: [
      {
        key: 'name',
        title: 'Name',
        type: 'string'
      },
      {
        key: 'price.value',
        title: 'Price',
        type: 'float'
      },
      {
        key: 'created_at',
        title: 'Created At',
        type: 'datetime'
      },
      {
        key: 'updated_at',
        title: 'Updated At',
        type: 'datetime'
      },
    ],
    title: 'Products'
  };
  @ViewChild('table') table: TableViewComponent<Product>;

  constructor(route: ActivatedRoute) {
    // console.log(this.context);
    route.data.subscribe(data => {
      this.data = data.products;
      this.context.data = this.data;
    });
  }

}
